import React, {useEffect, useState} from 'react';
import {getCurrentUserInfo} from "../../services/redux/reducers/user/user-reducer";
import {useDispatch, useSelector} from '../../hooks';
import './dashboard.scss'
import MapLayers from "./map-component/map";
import D3Project from "./tree-component/d3-project";
import CardHeader from "../../ui-components/card-header/card-header";
import { getProjects, updateProject } from '../../services/redux/reducers/project/projects.slice';
import { CreateProjectResponse } from '../../typings/api';
import { ActiveNodesType, MapCoordsType, ProjectData, TreeNodeType } from '../../typings';
import ProjectExplorer from '../../ui-components/project/explore/ProjectExplorer.component';
import { addNodeToTree, loadKnowledgeTree, updateTreeNode } from '../../services/redux/reducers/project/knowledge-tree.slice';
import { loadProjectNodeTypes } from '../../services/redux/reducers/project/project-node-types.slice';
import { getStatus, newTreeNode, sortDataTableProjects } from '../../utils';
import FavBlock from '../../ui-components/favorite-block/FavBlock.component';
import InvitationContainer from "./invitation-panel";
import ProjectDataTable from '../../ui-components/project/explore/ProjectDataTable.component';
import {MapViewChange, ProjectViewChange} from "../../services/redux/reducers/auth/auth.slice";
import { useTranslation } from 'react-i18next';
import Progress from '../../ui-components/commons/progress/Progress.component';
import CirclePlusIcon from '../../ui-components/icons/CirclePlus.icon';
import { DropdownSelectCell, EditableCell } from '../../ui-components/commons/table';
import { loadProjectNodeStatuses } from '../../services/redux/reducers/project/project-node-statuses.slice';
import Overlay from '../../ui-components/overlay/Overlay.component';
import { disableActivity, enableActivity } from '../../services/redux/reducers/map/map.slice';
import { index } from 'd3';
import {checkNodeData} from "../../services/redux/reducers/forge/forge.slice";
import Profil from "../profil/profil";
import IFCContainer from "./ifc_container";


const Dashboard = () => {
    const dispatch = useDispatch();
    const {projects: storeProjects, knowledgeTree, projectNodeTypes, projectNodeStatuses, mapMeta } = useSelector(state => state);
    const nodeData = useSelector(state => state.nodeData);
    const tab = useSelector<string>((state) => state.pages.selected_tab);
    const map_active = useSelector<boolean>((state) => state.pages.map_view);
    const [activeNodes, setActiveNodes] = useState<any>({});
    const [activeNodeId, setActiveNodeId] = useState<any>('');
    const [urn, setUrn] = useState(false);
    const [active, setActive] = useState<TreeNodeType | null>();
    const [treeData, setTreeData] = useState<any>(newTreeNode);
    const [projects, setProjects] = useState<Array<ProjectData | CreateProjectResponse>>();
    const [exploreProjectId, setExploreProjectId] = useState<any>();
    const [mapCoords, setMapCoords] = useState<MapCoordsType>();
    const [newColumnClick, setNewColumnClick] = useState(false);
    const [newColumnName, setNewColumnName] = useState('');
    const [newRowClick, setNewRowClick] = useState(false);
    const [newColumns, setNewColumns] = useState<Array<string>>([]);

    const { t } = useTranslation();

    const setProjectView = (value:boolean) => {
        if (knowledgeTree.payload)
            dispatch(ProjectViewChange({is_active: value, name: knowledgeTree.payload.name}))
    }

    useEffect(() => {
        dispatch(getCurrentUserInfo());
        dispatch(getProjects());
        dispatch(loadProjectNodeTypes(exploreProjectId));
        dispatch(loadProjectNodeStatuses(exploreProjectId));
    }, []);

    useEffect(() => {
        if (!storeProjects.isLoading && storeProjects.payload) {
            setProjects(storeProjects.payload);
            if (storeProjects?.payload?.length > 0) {
                setExploreProjectId((prev: any) => prev ? prev : storeProjects?.payload[0]?.id)
            }
        }
    }, [storeProjects]);

    useEffect(() => {
        if (exploreProjectId) {
            if (exploreProjectId !== "new-project")
                dispatch(loadKnowledgeTree(exploreProjectId));
            else setTreeData(newTreeNode);
        }
    }, [exploreProjectId]);

    useEffect(() => {
        if (!knowledgeTree.isLoading && knowledgeTree.payload) {
            setTreeData(knowledgeTree.payload);
        }
    }, [knowledgeTree])

    useEffect(() => {
        if (typeof active !== 'undefined' && active !== null) {
            dispatch(MapViewChange(true))
            let node = active;
            let parentIds: ActiveNodesType = {};
            while (node !== null && node !== undefined) {
                if (node?.data?.attributes?.id && node?.parent?.data?.attributes?.id) {
                    clickOnNode(node);
                    parentIds = {
                        ...parentIds,
                        [node?.parent?.data?.attributes?.id as string]: node.parent,
                        [node?.data?.attributes?.id as string]: node,
                    };
                }
                node = node?.parent as TreeNodeType;
            }
            setActiveNodes(parentIds);
            setProjectView(true);
        } else {
            setProjectView(false);
        }
    }, [active]);

    const clickOnNode = (node: TreeNodeType) => {
        setActiveNodeId(node.data.attributes.id.toString());
        if (node.data.attributes.nodeType === "ifc") {
            dispatch(checkNodeData(node.data.attributes.id.toString()))
            dispatch(MapViewChange(false))
        }
        // can add any click on node type interaction here
        else if (node.data.attributes.nodeType !== "ifc") {
            dispatch(MapViewChange(true))
        }
    }

    useEffect(() => {
        console.log(map_active)
    }, [map_active]);

    useEffect(() => {
        if (nodeData.response.errors.length > 0) {
            setUrn(false);
        } else if (nodeData.response.content != null) {
            setUrn(true);
        }
    }, [nodeData]);




    const handleAddNewNode = ({ id, node}: {id: any; node: any;}) => {
        dispatch(addNodeToTree({id, node: {
            description: node?.attributes?.description,
            name: node?.name,
            nodeType: node?.attributes?.nodeType ?? 'libre',
        }
        })).unwrap().then(() => {
            const idx = newColumns?.findIndex(f => f === node.name);
            console.log('AA', { newColumns, idx, node });
            if (idx > -1) {
                // remove the column
                const aa = [ ...newColumns ];
                aa.splice(idx, 1);
                setNewColumns(aa);
            }
        });
    }
    const handleAddNewColumn = (name: string) => {
        if (name?.trim()) {
            setNewColumns([ ...newColumns, name ]);
            /*
            projects?.forEach(async (p) => {
                await dispatch(addNodeToTree({id: p?.id, node: {
                    description: '',
                    name,
                    nodeType: 'libre'
                }
                }));
            });
            */
            setNewColumnName('');
        }
        setNewColumnClick(false);
    }
    const handleUpdateNode = ({ id, node }: { id: string | number; node: any;}) => {
        dispatch(updateTreeNode({ id, node }));
    }
    const handleBlurNewColumn = (name: string) => {
        setNewColumnName(name);
        setNewColumnClick(false);
    }
    const handleCreateDismiss = () => {
        dispatch(enableActivity('create_project'));
        setNewRowClick(false);
    }
    const handleNewRowClick = () => {
        setNewRowClick(true);
        dispatch(disableActivity('create_project'));
    }
    const handleUpdateProject = ({ id, node}: { id: string | number; node: CreateProjectResponse}) => {
        dispatch(updateProject({ id, node }));
    }

    return (
        <>
            {tab === "Invitation" && (
                <InvitationContainer project={exploreProjectId}/>
            )}
            <div className={"dashboard-layout"}>
                <div className={"explorer"}>
                    <CardHeader title={"Explorer"} search={
                        <ProjectExplorer
                            active={active}
                            activeNodes={activeNodes}
                            setActive={setActive}
                            setActiveNodes={setActiveNodes}
                            projects={projects}
                            selectedProjectId={exploreProjectId}
                            setSelectedProjectId={setExploreProjectId}
                        />
                    }/>
                    <FavBlock favs={projectNodeTypes.payload ?? []}/>
                    <div className={"canvas-explorer"}>
                        <D3Project
                            active={active}
                            treeData={treeData}
                            activeNodes={activeNodes}
                            setActive={setActive}
                            setActiveNodes={setActiveNodes}
                            selectedProjectId={exploreProjectId}
                            mapCoords={mapCoords}
                        />
                    </div>
                </div>
                <div className={"project"}>
                    <CardHeader title={`Contextualiser (${active?.data?.name ? active?.data?.name : "Mes projets"})`}
                                search={"Carte"}/>
                    <div className={"map-container"}>
                        <MapLayers onPin={setMapCoords} pins={projects}/>
                        { !map_active && (
                                <IFCContainer has_urn={urn} nodeId={activeNodeId} />
                            )
                        }
                    </div>
                    {
                        projects?.length > 0 ? (
                            <ProjectDataTable
                                id="project-data-table-10x"
                                data={([...(projects as ProjectData[])]).sort(sortDataTableProjects)}
                                meta={{
                                    name: {
                                        displayName: t('project-name'),
                                        render: (p, index) => (
                                            <div style={{
                                                padding: 0,
                                                background: '#fff',
                                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                                borderTopRightRadius: index === 0 ? 5 : 0,
                                            }}>
                                                <EditableCell
                                                    style={{ textAlign: 'right' }}
                                                    onEnter={(val) => handleUpdateProject({ id: p.id, node: { name: val }})}
                                                    defaultValue={p.name}
                                                />
                                            </div>
                                        )
                                    },
                                    creator: {
                                    displayName: t('project-manager'),
                                    render: (p, index) => (
                                        <div  style={{
                                                padding: '0 5px',
                                                background: '#fff',
                                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                                borderTopRightRadius: index === 0 ? 5 : 0,
                                            }}
                                            title={`${p.creator.firstName ?? ''} ${p.creator.lastName ?? ''}`}
                                        >
                                            <EditableCell
                                                readonly
                                                style={{ textAlign: 'right' }}
                                                onEnter={(val) => console.log(val)}
                                                defaultValue={`${p.creator.firstName ?? ''} ${p.creator.lastName ?? ''}`}
                                            />
                                        </div>
                                    )
                                    },
                                    progression: {
                                        displayName: t('progression'),
                                        render: (p, index) => (
                                            <div style={{
                                                display: 'flex',
                                                background: '#fff',
                                                borderBottom: '2px solid #fff',
                                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                                borderTopRightRadius: index === 0 ? 5 : 0,
                                            }}>
                                                <Progress color="#ED6E40" progress={50} />
                                            </div>
                                        )
                                    },
                                    status: {
                                        displayName: t('status'),
                                        render: (p: any) => {
                                            const st = getStatus((p?.tree ? p?.tree?.attributes?.status : p?.attributes?.status ));
                                            return (
                                                <DropdownSelectCell
                                                    options={projectNodeStatuses.payload}
                                                    selected={st.status}
                                                    onChange={(status) => handleUpdateNode({ id: p.id, node: { id: p?.tree?.attributes?.id, status }})}
                                                    color={st.color}
                                                />
                                            )
                                        }
                                    },
                                }}
                                treeMeta={{
                                    status: {
                                        render: (pr, p) => {
                                            const st = getStatus((p?.tree ? p?.tree?.attributes?.status : p?.attributes?.status));
                                            return (
                                                <DropdownSelectCell
                                                    options={projectNodeStatuses.payload}
                                                    selected={st.status}
                                                    onChange={(status) => handleUpdateNode({ id: pr.id, node: { id: p?.attributes?.id, status }})}
                                                    color={st.color}
                                                />
                                            )
                                        }
                                    },
                                    none: {
                                        render: (p, node) => <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => handleAddNewNode({ id: p.id, node })} />
                                    }
                                }}
                                newColumn={{
                                    renderData: (index) => {
                                        return <div style={{
                                                display: 'flex',
                                                background: '#fff',
                                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                                borderTopRightRadius: index === 0 ? 5 : 0,
                                        }}>
                                            <EditableCell readonly />
                                        </div>
                                    },
                                    renderHeader: () => newColumnClick ? (
                                        <EditableCell
                                            autoFocus
                                            style={{ textAlign: 'right' }}
                                            onBlur={handleBlurNewColumn}
                                            onEnter={handleAddNewColumn}
                                            defaultValue={newColumnName}
                                        />
                                    ) : <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => setNewColumnClick(true)} />
                                }}
                                newRow={{
                                    render: (d, index, len) => {
                                        return (
                                            <div style={{
                                                display: 'flex',
                                                background: '#fff',
                                                borderBottomLeftRadius: index === 0 ? 5 : 0,
                                                borderBottomRightRadius: index === len - 1 ? 5 : 0,
                                        }}>
                                            { index === 0 ? (
                                                <span style={{ padding: '0 10px' }} title={t('use-map-to-select-location')}>
                                                    <CirclePlusIcon style={{ height: 20, width: 20, marginTop: -5 }} onClick={handleNewRowClick} />
                                                </span>
                                            ) :  <span style={{ visibility: 'hidden' }}>h</span> }
                                        </div>
                                        )
                                    }
                                }}
                                appendColums={newColumns}
                            />
                        ) : null
                    }
                    {
                        newRowClick ? (
                            (
                                <Overlay show={newRowClick} el="#project-data-table-10x" container="#project-data-table-10x" onDismiss={handleCreateDismiss}>
                                    <code>{mapMeta.selection.description}</code>
                                    <blockquote>{t('use-map-to-select-location')}</blockquote>
                                </Overlay>
                            )
                        ) : null
                    }
                </div>
            </div>
        </>
    )
}
export default Dashboard;