import { DataTableProps, ProjectData } from "../../../typings";
import HeaderRow from "../../commons/table/HeaderRow.component";
import './project-table.scss';

const ProjectDataTable = ({ data, meta, treeMeta, newColumn, newRow, appendColums, ...props }: DataTableProps<ProjectData>) => {
    const renderData = (d: any) => {
        if (typeof d === 'string') {
            return d;
        }
        return null;
    }
    const handleFocusOnEnter = (id: string, e: any) => {
        const target = document.getElementById(id);
        if (e?.key && e?.key?.toLowerCase() === 'enter' && target) {
          (target?.nextElementSibling?.querySelector('input:not(:read-only)') as HTMLElement)?.focus()
        }
    }
    const extraHeaders: { [key: string]: any; } = {};
    if (data?.length > 0) {
      data.forEach((d) => {
        if (d.tree?.children?.length) d?.tree?.children?.forEach((v) => {
          if (v.name) extraHeaders[v.name] = v;
        })
      })
    }
    const newRows = [...Object.keys({...(meta ?? {}), ...(extraHeaders ?? {})}), ...appendColums, 'empty'];
    return data?.length > 0 ? (
        <div {...props} style={{ maxWidth: '100vw', overflow: 'auto', maxHeight: '20vh', overflowY: 'auto' }}>
          <table className="project-table" style={{ borderCollapse: 'collapse', borderRadius: 5 }}>
            <thead>
              <HeaderRow>
                <>
                  {
                    Object.entries(meta).map(([k, d]) => <th key={k}>{d.displayName ?? k}</th>)
                  }
                  {
                   Object.keys(extraHeaders).map(v => <th style={{ textTransform: 'capitalize' }} key={v}>{v}</th>)
                  }
                  {
                   appendColums.map(v => <th style={{ textTransform: 'capitalize' }} key={v}>{v}</th>)
                  }
                  {
                    <th style={{ textAlign: 'center' }}>{newColumn.renderHeader()}</th>
                  }
                </>
              </HeaderRow>
            </thead>
            <tbody>
                {
                    data.map((dt, idx) => (
                        <tr key={dt.id} id={dt?.id?.toString()} onKeyDown={(e) => handleFocusOnEnter(dt?.id?.toString(), e)}>
                            {
                                Object.entries(meta).map(([k, d]) => <td key={k}>{d.render ? d.render(dt, idx) : renderData(dt[k as keyof ProjectData]) }</td>)
                            }
                            {
                              Object.values(extraHeaders).map(v => {
                               if (dt?.tree?.children && dt?.tree?.children.length > 1) {
                                  const it = dt?.tree?.children?.find(f => f.name === v?.name);
                                  if (it) return <td data-key={it.name + dt.id} key={it.name + dt.id}>{ treeMeta['status']?.render(dt, it, idx)}</td>;
                                  return <td key={dt.id + v?.name}>{ treeMeta['none']?.render(dt, v, idx) }</td>
                               } else {
                                 return <td key={dt.id + v?.name}>{ treeMeta['none']?.render(dt, v, idx) }</td>
                               }
                              })
                            }
                            {
                              appendColums.map(v => <td key={dt.id + v}>{ treeMeta['none']?.render(dt, { name: v }, idx) }</td>)
                            }
                            {
                              <td key={dt.id + 'new'}>{newColumn.renderData(idx)}</td>
                            }
                        </tr>
                    ))
                }
                {
                  newRow && (
                    <tr>
                      {
                          newRows.map((k, i) => <td key={k}>{newRow.render(k, i, newRows.length)}</td>)
                      }
                    </tr>
                  )
                }
            </tbody>
            <tfoot>
              <tr>
                {
                    newRows.map((k, i) => <td key={k}><span style={{ visibility: 'hidden', display: 'flex' }}>h</span></td>)
                }
              </tr>
            </tfoot>
          </table>
        </div> 
    ) : null;
};

export default ProjectDataTable;
