import { useState } from "react";
import { useTranslation } from "react-i18next";
import { getStatus } from "../../../utils";

export interface DropdownCellPropType {
    onChange: (text: string) => void;
    onEnter?: (text: string) => void;
    editable?: boolean;
    selected?: string;
    style?: any;
    className?: string;
    color?: string;
    options?: Array<{ id?: string; label?: string; [key: string]: string; }>;
    [key: string]: any;
}
const DropdownSelectCell = ({  onChange, onEnter, style, className, options, selected, color, editable, ...props }: DropdownCellPropType) => {
    const { t } = useTranslation();
    const selection = options?.find(f => f?.label === selected);
    const [open, setOpen] = useState(false);
    const [val, setVal] = useState('');
    const handleBlur = () => {
        setTimeout(() => setOpen(false), 100);
    }
    const handleClick = (val: string) => {
        onChange(val);
    }
    return (
        <div style={{
            ...{
                width: '100%',
                background: color ?? 'transparent',
                border: 'none',
                outline: 'none',
                margin: 0,
                lineHeight: 'inherit',
                fontFamily: 'inherit',
                fontSize: 'inherit',
                position: 'relative',
            }, ...(
                style ?? {}
            )
        }}
        {...props}
        className={`dropdown-select-cell-sb ${ className ?? '' }`}
        >
        <input
            style={{
                width: '100%',
                background: 'transparent',
                border: 'none',
                outline: 'none',
                margin: 0,
                lineHeight: 'inherit',
                fontFamily: 'inherit',
                fontSize: 'inherit',
                position: 'relative',
                cursor: 'pointer',
            }}
            className="dropdown-select-cell-input"
            value={val || t(selection?.label ?? '')}
            onChange={(e: any) => setVal(e?.target?.value)}
            onFocus={() => setOpen(true)}
            readOnly={!editable}
            onBlur={handleBlur}
        />    
        {
            options?.length > 0 && open && (
                <ul className="dropdown-select-list-group-sb">
                    {
                        options?.map((opt) => (
                            <li
                                style={{ background: getStatus(opt?.label).color ?? '#fff' }}
                                key={opt?.label + opt?.id} value={opt?.id ?? opt?.label}
                                className={`dropdown-select-list-item-sb ${selection?.label === opt?.label || selection?.id === opt?.id ? 'selected' : ''}`}
                            >
                                <button className="dropdown-select-button" onClick={() => handleClick(opt?.label ?? opt?.id)}>{t(opt?.label ?? '')}</button>
                            </li>
                        ))
                    }
                </ul>
            )
        }
        </div>
    )
}

export default DropdownSelectCell;
