import React, {useEffect, useRef, useState} from 'react';
import ForgeViewer from "./forge-viewer/Forgeview";
import {useDispatch, useSelector} from "../../hooks";
import {
    ForgeResponseState,
    getCheckStatus,
    getForgeToken,
    setURN,
    URNLoadState
} from "../../services/redux/reducers/forge/forge.slice";
import axios from "axios";

type IFCContainerProps = {
    urn?: string,
    has_urn: boolean,
    nodeId: string
};

const IFCContainer = (props: IFCContainerProps) => {
    const forgeRef = useRef<any>();
    const dispatch = useDispatch();
    const [uploaded, setUploaded] = useState(false);
    const forgeToken = useSelector<ForgeResponseState>((state) => state.forge);
    const urn = useSelector<String>((state) => state.urn.urn);
    const urn_load = useSelector<URNLoadState | null>((state) => state.urn_load.response.content);
    const nodeData = useSelector(state => state.nodeData);
    let time_load = 3000;

    useEffect(() => {
        dispatch(getForgeToken());
    }, []);


    const onUpload = (e:any) => {
        if (e.target.files[0]) {
            const file = new FormData()
            file.append("file", e.target.files[0]);
            axios.post("http://localhost:8080/api/v1/public/forge/upload/"+props.nodeId, file).then((res) => {
                if (res.data.content.result === "success") {
                    dispatch(setURN(res.data.content.urn))
                    dispatch(getCheckStatus(res.data.content.urn))
                }
            })
        }
    }

    const getUploadCheck = () => {
        dispatch(getCheckStatus(urn));
    }

    useEffect(() => {
        if (urn_load?.status !== "success") {
            if (urn.length > 0) {
                setTimeout(() => {
                    getUploadCheck();
                    time_load += 1500;
                }, time_load)
            }
        } else {
            setUploaded(true)
        }
    }, [urn_load]);

    const uploadOrDisplay = () => {
        if (forgeToken.response.content?.access_token.length > 0) {
            if (props.has_urn && nodeData.response.content != null) {
                return (
                    <ForgeViewer
                        version="6.0"
                        expiresAt={3599}
                        token={forgeToken.response.content?.access_token}
                        urn={nodeData.response.content.data}
                        headless={false}
                        onSelectedGuid={""} />
                )
            } else {
                if (!(urn.length > 0)) {
                    return (
                        <input className={"center-input"} type="file" name="upload" onChange={(e) => onUpload(e)}/>
                    )
                } else if (!uploaded && urn.length > 0) {
                    return (
                        <p className={"center-input"}>{urn_load?.status}</p>
                    )
                } else if (forgeToken.response.content?.access_token && urn.length > 0 && uploaded) {
                    return (
                        <ForgeViewer
                            version="6.0"
                            expiresAt={3599}
                            token={forgeToken.response.content?.access_token}
                            urn={urn}
                            headless={false}
                            onSelectedGuid={""} />
                    )
                }
            }
        }
    }

    return (
        <div className={"adsk-viewing-viewer overlay-map"}>
            {uploadOrDisplay()}
        </div>
    );
};

export default IFCContainer;
